// src/App.js
import React, { useState } from "react";
import Histogram from "./Histogram";
import DistributorSelector from "./DistributorSelector";
import RandLibLogo from "./RandlibLogo"
import {
  randint,
  uniform,
  normal,
  cauchy,
  exponential,
  chisquare
} from "randlibjs";
import "./App.css";

const DISTRIBUTIONS = [
  { name: "randint", sampler: parameters => randint(parameters.low, parameters.high, parameters.size), parameters: { low: 0.0, high: 6.0 } },
  { name: "uniform", sampler: parameters => uniform(parameters.low, parameters.high, parameters.size), parameters: { low: 0.0, high: 1.0 } },
  { name: "normal", sampler: parameters => normal(parameters.loc, parameters.scale, parameters.size), parameters: { loc: 0.0, scale: 1.0 } },
  { name: "cauchy", sampler: parameters => cauchy(parameters.median, parameters.gamma, parameters.size), parameters: { median: 0.0, gamma: 1.0 } },
  { name: "exponential", sampler: parameters => exponential(parameters.lambda, parameters.size), parameters: { lambda: 1.0 } },
  { name: "chisquare", sampler: parameters => chisquare(parameters.k, parameters.size), parameters: { k: 6.0 } },
]

const DEFAULT_DISTRIBUTION = 0

function computeHistogram(muestra, numCajas) {
  muestra.sort((a, b) => a - b);
  const min = muestra[0];
  const max = muestra[muestra.length - 1];
  const rango = max - min;
  const tamanoCaja = rango / numCajas;
  const cajas = Array(numCajas).fill(0);

  muestra.forEach(valor => {
    const indiceCaja = Math.min(Math.floor((valor - min) / tamanoCaja), numCajas - 1);
    cajas[indiceCaja]++;
  });

  // Generate labels
  const labels = [];
  for (let i = 0; i < numCajas; i++) {
    const start = min + i * tamanoCaja;
    const end = start + tamanoCaja;
    labels.push(`${start.toFixed(1)}-${end.toFixed(1)}`);
  }

  return {
    labels,
    values: cajas,
  };
}

function getSamplerForDistribution(distributionName) {
  return DISTRIBUTIONS.find(distribution => distribution.name === distributionName).sampler
}

function copyToClipboard(id) {
  const codeElement = document.querySelector(id);
  const range = document.createRange();
  range.selectNode(codeElement);
  window.getSelection().removeAllRanges();
  window.getSelection().addRange(range);
  try {
    document.execCommand('copy');
  } catch (err) {
    // pass
  }
  window.getSelection().removeAllRanges();
}

const App = () => {
  const [selectedDistribution, setSelectedDistribution] = useState(DISTRIBUTIONS[DEFAULT_DISTRIBUTION].name);
  const [selectedParameters, setSelectedParameters] = useState(DISTRIBUTIONS[DEFAULT_DISTRIBUTION].parameters);
  const [sample, setSample] = useState(() => {
    return getSamplerForDistribution(selectedDistribution)({ ...selectedParameters, size: 1000 })
  });

  // Functions
  function handleDistributionSelect(e) {
    const distributionName = e.target.value
    setSelectedDistribution(distributionName)
    const newSelectedDistribution = DISTRIBUTIONS.find(distribution => distribution.name === distributionName)
    setSelectedParameters(newSelectedDistribution.parameters)
    resample(newSelectedDistribution.name, newSelectedDistribution.parameters)
  }
  function handleParamChange(name, value) {
    const newParameters = { ...selectedParameters, [name]: Number(value) }
    setSelectedParameters(newParameters);
    resample(selectedDistribution, newParameters)
  };
  function resample(distributionName, parameters) {
    const sampler = getSamplerForDistribution(distributionName)
    try {
      const newSample = sampler({ ...parameters, size: 1000 })
      setSample(newSample)
    }
    catch (error) {
      setSample(null)
      console.log("Parmetro incorrecto.")
    }
  }

  const bins = 100;
  let histogramContent = null
  if (sample) {
    const histogram = computeHistogram(sample, bins);
    histogramContent = <Histogram data={histogram} />
  }
  else {
    histogramContent = <p>Incorrect parameters</p>
  }

  return (
    <>
    <div className="App">
      <div className="main">
        
        <h1><RandLibLogo size={32}/><a href="https://www.npmjs.com/package/randlibjs" className="library-link">Randlibjs</a></h1>
        <h2>Powerful Lightweight Random Number Generators</h2>
        <p>
          Randlibjs is a powerful JavaScript library designed to provide a wide range of random number distributions efficiently. Whether you need uniform, normal, exponential, or other distributions, Ranlib.js makes it easy to generate pseudo-random samples.
        </p>
        <p>
          The library supports various statistical distributions, giving you flexibility for simulations, games, data analysis, and more. While the showcase here focuses on random number generation and histogram visualization, Randlibjs also offers additional powerful methods such as seeding, shuffling, and permutation for even greater control over your random processes.
        </p>
        <p>
          Explore the full potential of random number generation with Randlibjs today!
        </p>
        <h2>Installation</h2>
        <p>
        To get started with Randlibjs, you can install it via npm. Simply run the following command in your terminal:
        </p>
        <div className="code-box">
          <code id="npm-command">npm install randlibjs</code>
        </div>
        <button className="copy-btn" onClick={() => copyToClipboard('#npm-command')}>Copy</button>
    
        <h2>Usage</h2>
        <p>
        After installing the library, you can import it into your project and start generating random numbers. Here's an example of how to generate random numbers using a standard normal distribution:
        </p>
        <div className="code-box">
          <code id="example-code">
            import normal from 'randlibjs';<br/><br/>

            // Generate a sample from a standard normal distribution<br/>
            const sample = normal(low=0.0, high=1.0, size=10);<br/><br/>
            // Output result sample<br/>
            console.log(sample);
          </code>
        </div>
        <button className="copy-btn" onClick={() => copyToClipboard('#example-code')}>Copy</button>
      <div>
      <h2>Showcase</h2>
      <p>
        On this page, you can interactively generate random numbers and visualize the results with a dynamic histogram. Simply select the distribution you want, adjust its parameters to suit your requirements, and watch the sample data update in real time.
      </p>
      {histogramContent}
      </div>
      <DistributorSelector distributions={DISTRIBUTIONS}
        selectedDistribution={selectedDistribution}
        handleDistributionSelect={handleDistributionSelect}
        parameters={selectedParameters}
        handleParamChange={handleParamChange}
        resample={() => resample(selectedDistribution, selectedParameters)} />
    </div>
    <div>
      <p>
      Visit official Randlibjs npm page in <a href="https://www.npmjs.com/package/randlibjs" className="library-link">this link</a>.
      </p>
    </div>
    </div >

    <footer className="footer">
        <div className="text-center">
            <small className="copyright">Designed by <a href="https://pereradrian.github.io" target="_blank">Adri Mu</a></small>
        </div>
    </footer>
    </>
  );
};

export default App;
