import React from 'react';
import { randint, uniform } from 'randlibjs';

function RandLibLogo({size}) {
    
    let numberOfLayers = randint(3, 10)
    const centers = []
    const radius = []
    for (let layer = 0; layer < numberOfLayers; layer ++) {
        while (1 / (layer+1) / (layer+1) < uniform()) {
            // Compute center and radius
            const centerAngle = uniform(0, Math.PI/2)
            const centerRadius = uniform(layer, layer+1) / numberOfLayers
            const x = centerRadius * Math.cos(centerAngle)
            const y = 1.0 - centerRadius * Math.sin(centerAngle)
            centers.push([size * x, size * y])
            const circleRadius = uniform(1/(layer+2)/(layer+2), 1/(layer+1)/(layer+1))
            radius.push(size * circleRadius)
        }
    }
    const baseRectangle = <rect x="0" y="0" width="32" height="32" rx="8" ry="8" fill="yellow" />
    return <svg xmlns="http://www.w3.org/2000/svg" xmlnsXlink="http://www.w3.org/1999/xlink" aria-hidden="true" role="img" className="randlib-logo" width={size} height={size} preserveAspectRatio="xMidYMid meet" viewBox={`0 0 ${size} ${size}`}>
    <defs>
    <clipPath id="clip-rect">
      {baseRectangle}
    </clipPath>
  </defs>
    {baseRectangle}
    <g clipPath="url(#clip-rect)">
        <circle cx="0" cy="32" r="8" fill="black" />
        {
            centers.map((_, index) => {
                const x = centers[index][0]
                const y = centers[index][1]
                const r = radius[index]
                return <circle key={index} cx={x} cy={y} r={r} fill="black" />
            })
        }
    </g>
  </svg>
    
}
export default RandLibLogo;